body {
  background: rgba(255, 255, 255, 1);
  margin: 0 auto;
  padding: 0;
}

@font-face {
  font-family: "Lato";
  src: local("Lato"), url("../fonts/lato/Lato-Regular.woff"), url("../fonts/lato/Lato-Regular.woff2");
}

@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/oswald/oswaldmedium.woff"), url("../fonts/oswald/oswaldmedium.woff2");
}

@font-face {
  font-family: "Oswald";
  src: local("Oswald"), url("../fonts/oswald/oswaldregular.woff"), url("../fonts/oswald/oswaldregular.woff2");
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
